@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.main {
  padding: 30px 55px;
  background: #E5E5E5;
  min-height: calc(100vh - 72px);
  display: flex;
  align-items: center;
}

.wrapper {
  height: 100%;
}

@media (max-width: 920px) {
  body {
    overflow: auto;
  }

  .main {
    height: inherit;
    padding: 30px 10px;
  }

  .main .home,
  .main .error {
    height: 83vh;
    overflow: hidden;
  }
}
.account {
    display: flex;
    flex: 1 1;
}

.account-info {
    width: 300px;
    margin-right: 96px;
    flex: 1 1;
    text-align: center;
}

.account-img img {
    border-radius: 50%;
    max-width: 280px;
}

.account-name {
    margin: 30px 0 12px;
    font-weight: 600;
    font-size: 26px;
    line-height: 130%;
}

.account-login {
    font-size: 18px;
    line-height: 22px;
    color: #0064EB;
    text-decoration: none;
}

.account-followings {
    display: flex;
    margin-top: 30px;
}

.account-followers {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    line-height: 150%;
}

.account-followers span,
.account-followers span {
    margin: 0 5px 0 7px;
}

.account-followers:first-child {
    margin-right: 25px;
}

.account-repos {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.account-repos-empty {
    text-align: center;
}

.account-repos-empty img {
    height: 62px;
    width: 76px;
}

.account-repos-empty p {
    font-size: 22px;
    line-height: 140%;
    color: #808080;
    margin-top: 48px;
}

.account-repos-list h2 {
    font-weight: 600;
    font-size: 32px;
    line-height: 130%;
    margin-bottom: 29px;
}

@media (max-width: 920px) {
    .account {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .account-repos {
        margin-top: 30px;
    }

    .account-info {
        max-width: 200px;
        margin-right: 0px;
        margin: 0 auto;
    }

    .account-img img {
        max-width: 180px;
    }
}
.error {
  height: 100%;
  flex:1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.error-text {
  font-size: 22px;
  line-height: 140%;
  color: #808080;
  margin-top: 42px;
}
.header {
    height: 72px;
    width: 100%;
    background: #0064EB;
    border-bottom: 1px solid #D4D8DD;
    padding: 0 41px;
    display: flex;
    align-items: center;
}

.header-logo {
    margin-right: 22px;
}

.header-search {
    position: relative;
}

.header-search input {
    padding: 0 44px;
    width: 500px;
    height: 40px;
    border: none;
    outline: none;
    background: #FFFFFF;
    border-radius: 6px;
    font-size: 14px;
}

.header-search img {
    position: absolute;
    top: 50%;
    left: 19px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    pointer-events: none;
}

@media (max-width: 920px) {
    .header {
        justify-content: flex-start;
    }

    .header-search input {
        width: 210px;
        padding: 0 5px 0 44px;
    }

    .header-logo {
        margin-right: 10px;
    }
    
}
.home {
    height: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.home-text {
    font-size: 22px;
    line-height: 140%;
    color: #808080;
    margin-top: 42px;
    width: 210px;
    text-align: center;
}

@media (max-width: 920px) {
    .home-text {
        font-size: 18px; 
    }
}
.paginate {
    width:100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 20px;
}

.paginate-items {
    font-size: 14px;
    line-height: 21px;
    color: #808080;
}

.pagination {
    display: flex;
    list-style: none;
    margin-left: 23px;
}

.pagination li {
    border-radius: 3px;
}

.pagination li+li {
    margin-left: 3px;
}

.pagination a {
    padding: 5px;
    border-radius: 3px;
    cursor: pointer;
    transition: .15s;
}

.pagination a:hover,
.pagination li.active a {
    background: #0064EB;
    color: #fff;
}

.pagination .previous,
.pagination .next {
    color: #0064EB;
}

.pagination .previous.disabled,
.pagination .next.disabled {
    color: #808080;
}
.repositories .repos {
    width: 100%;
    background: #fff;
    border-radius: 6px;
    padding: 24px 32px;
}

.repositories .repos+.repos {
    margin-top: 24px;
}

.repositories .repos-title {
    display: inline-block;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: #0064EB;
    margin-bottom: 16px;
    text-decoration: none;
}

.repositories .repos-description {
    font-size: 16px;
    line-height: 19px;
}

@media (max-width: 920px) {
    .repositories .repos-description {
        font-size: 11px;
    }
}
